import io from "socket.io-client"
import { readable, writable } from "svelte/store"

import statusIndex from "./statusIndex.js"



const socket = io("https://gui3-server.glitch.me/", {
  withCredentials: true,
  extraHeaders: {
    "my-custom-header": "abcd"
  },
  autoConnect: false,
  reconnection: true
})

const status_store = readable(statusIndex["_unconnected"], function start (set) {
  let lastEvent = "_unconnected"
  
  function listener (evt, ...args) {
    if (statusIndex[evt] && evt !== "message") {
      set(statusIndex[evt])
      lastEvent = evt
    } else {
      set({
        name: evt,
        color: "#fc0",
        description: args.join(",")
      })
      lastEvent = evt
      setTimeout(_ => {
        if (lastEvent === evt) {
          set(statusIndex["connect"])
        }
      }, 1000)
    }
  }

  socket.onAny(listener)

  return function stop () {
    console.log("stop")
    socket.offAny(listener)
  }
})


export {
  socket,
  status_store
}

